.admin-bg {
  min-height: 100vh;
  background: #ffe4ec;
  font-family: 'ZCOOL KuaiLe', 'Comic Sans MS', cursive, sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
}

.admin-content {
  display: flex;
  align-items: center;
  justify-content: center;
}

.development-card {
  text-align: center;
  background: rgba(255, 255, 255, 0.9);
  padding: 60px 80px;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(231, 84, 128, 0.15);
  border: 2px solid rgba(231, 84, 128, 0.1);
}

.icon-container {
  margin-bottom: 30px;
}

.dev-icon {
  font-size: 4rem;
  animation: rotate 3s linear infinite;
}

@keyframes rotate {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.development-card h1 {
  color: #e75480;
  font-size: 2.5rem;
  margin-bottom: 20px;
  text-shadow: 1px 2px 10px #fff0f6;
}

.development-card p {
  color: #666;
  font-size: 1.2rem;
  margin: 0;
}

.highlight {
  background: #fff0f6;
  color: #e75480;
  border-radius: 6px;
  padding: 2px 8px;
  font-weight: bold;
}
