.profile-bg {
  min-height: 100vh;
  background: #ffe4ec;
  font-family: 'ZCOOL KuaiLe', 'Comic Sans MS', cursive, sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile-content {
  display: flex;
  align-items: center;
  justify-content: center;
}

.development-card {
  text-align: center;
  background: rgba(255, 255, 255, 0.9);
  padding: 60px 80px;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(231, 84, 128, 0.15);
  border: 2px solid rgba(231, 84, 128, 0.1);
}

.icon-container {
  margin-bottom: 30px;
}

.dev-icon {
  font-size: 4rem;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-15px); }
  60% { transform: translateY(-7px); }
}

.development-card h1 {
  color: #e75480;
  font-size: 2.5rem;
  margin-bottom: 20px;
  text-shadow: 1px 2px 10px #fff0f6;
}

.development-card p {
  color: #666;
  font-size: 1.2rem;
  margin: 0;
}

.highlight {
  background: #fff0f6;
  color: #e75480;
  border-radius: 6px;
  padding: 2px 8px;
  font-weight: bold;
}
